@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

.search-box {
	background: transparent;
	border: 1px solid black;
	backdrop-filter: none;
	transition: all 0.5s ease;
}

.search-box:focus {
	background: transparent;
	box-shadow: 0px 0px 10px rgba(49, 66, 252, 0.577);
	border: 1px solid rgba(196, 196, 196, 0.194);
}

.search-btn:hover {
	box-shadow: 0px 0px 10px rgba(49, 66, 252, 0.577);
}
.inp:focus {
	box-shadow: none;
}

#home {
	transform: translateY(80px);
}

.your-notes {
	margin-bottom: 20px;
}

@media only screen and (max-width: 561px) {
	#home {
		transform: translateY(130px);
	}
}

html {
	width: 100%;
	height: 100%;
}

body {
	background: linear-gradient(to right, rgb(82, 179, 218), rgb(205, 249, 252));
	height: 100%;
}

button {
	background-color: rgb(44, 75, 254);
	box-shadow: none;
	border: none;
}

body::before {
	content: "";
	position: fixed;
	height: 100%;
	width: 100%;
	background: linear-gradient(#2196f3, #e91e63);
	top: 0;
	left: 0;
	clip-path: circle(30% at right 30%);
	opacity: 0.8;
	z-index: -1;
}

body::after {
	content: "";
	position: fixed;
	height: 100%;
	width: 100%;
	background: linear-gradient(#2196f3, #e91e63);
	top: 0;
	left: 0;
	clip-path: circle(20% at left 90%);
	opacity: 0.8;
	z-index: -1;
}

.inpl {
	background-color: rgba(216, 216, 216, 0.386);
	backdrop-filter: blur(5px);
	border: none;
	transition: all 0.5s ease;
}
.inpl:focus {
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.16);
	background-color: rgba(216, 216, 216, 0.338);
	transform: scale(1.004);
}

.card-b {
	background-color: rgba(216, 216, 216, 0.338);
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.16);
	border: none;
	backdrop-filter: blur(5px);
	width: 90%;
	margin: auto;
}
.addsomenotes {
	font-family: "Lato", sans-serif;
}
