html {
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
}

* {
	box-sizing: inherit;
}
*:before,
*:after {
	box-sizing: inherit;
}

body .dribbble {
	position: fixed;
	display: block;
	right: 24px;
	bottom: 24px;
}
body .dribbble img {
	display: block;
	width: 76px;
}
