html {
	width: 100%;
	height: 100%;
}

body {
	background: linear-gradient(to right, rgb(82, 179, 218), rgb(205, 249, 252));
	height: 100%;
}

button {
	background-color: rgb(44, 75, 254);
	box-shadow: none;
	border: none;
}

body::before {
	content: "";
	position: fixed;
	height: 100%;
	width: 100%;
	background: linear-gradient(#2196f3, #e91e63);
	top: 0;
	left: 0;
	-webkit-clip-path: circle(30% at right 30%);
	        clip-path: circle(30% at right 30%);
	opacity: 0.8;
	z-index: -1;
}

body::after {
	content: "";
	position: fixed;
	height: 100%;
	width: 100%;
	background: linear-gradient(#2196f3, #e91e63);
	top: 0;
	left: 0;
	-webkit-clip-path: circle(20% at left 90%);
	        clip-path: circle(20% at left 90%);
	opacity: 0.8;
	z-index: -1;
}

.inpl {
	background-color: rgba(216, 216, 216, 0.386);
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
	border: none;
	transition: all 0.5s ease;
}
.inpl:focus {
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.16);
	background-color: rgba(216, 216, 216, 0.338);
	transform: scale(1.004);
}

.card-b {
	background-color: rgba(216, 216, 216, 0.338);
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.16);
	border: none;
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
	width: 90%;
	margin: auto;
}
.addsomenotes {
	font-family: "Lato", sans-serif;
}

.inp {
	background: rgba(216, 216, 216, 0.386);
	transition: all 0.5s ease;
	outline: none;
	border: none;
	transform-style: preserve-3d;
}

.modal_body {
	perspective: 500px;
}

.modal-content {
	position: fixed;
	top: 40%;
	z-index: 2;
	background: rgba(216, 216, 216, 0.386) !important;
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
}

.content-body {
	margin: auto;
}

.inp:focus {
	outline: none;
	border: none;
	transform: scale(1.004);
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.16);
	background-color: rgba(216, 216, 216, 0.338);
}

.loader {
	--background: linear-gradient(135deg, #23c4f8, #275efe);
	--shadow: rgba(39, 94, 254, 0.28);
	--text: #6c7486;
	--page: rgba(255, 255, 255, 0.36);
	--page-fold: rgba(255, 255, 255, 0.52);
	--duration: 3s;
	width: 200px;
	height: 140px;
	position: relative;
}
.loader:before,
.loader:after {
	--r: -6deg;
	content: "";
	position: absolute;
	bottom: 8px;
	width: 120px;
	top: 80%;
	box-shadow: 0 16px 12px var(--shadow);
	transform: rotate(var(--r));
}
.loader:before {
	left: 4px;
}
.loader:after {
	--r: 6deg;
	right: 4px;
}
.loader div {
	width: 100%;
	height: 100%;
	border-radius: 13px;
	position: relative;
	z-index: 1;
	perspective: 600px;
	box-shadow: 0 4px 6px var(--shadow);
	background-image: var(--background);
}
.loader div ul {
	margin: 0;
	padding: 0;
	list-style: none;
	position: relative;
}
.loader div ul li {
	--r: 180deg;
	--o: 0;
	--c: var(--page);
	position: absolute;
	top: 10px;
	left: 10px;
	transform-origin: 100% 50%;
	color: var(--c);
	opacity: var(--o);
	transform: rotateY(var(--r));
	animation: var(--duration) ease infinite;
}
.loader div ul li:nth-child(2) {
	--c: var(--page-fold);
	animation-name: page-2;
}
.loader div ul li:nth-child(3) {
	--c: var(--page-fold);
	animation-name: page-3;
}
.loader div ul li:nth-child(4) {
	--c: var(--page-fold);
	animation-name: page-4;
}
.loader div ul li:nth-child(5) {
	--c: var(--page-fold);
	animation-name: page-5;
}
.loader div ul li svg {
	width: 90px;
	height: 120px;
	display: block;
}
.loader div ul li:first-child {
	--r: 0deg;
	--o: 1;
}
.loader div ul li:last-child {
	--o: 1;
}
.loader span {
	display: block;
	left: 0;
	right: 0;
	top: 100%;
	margin-top: 20px;
	text-align: center;
	color: var(--text);
}

@keyframes page-2 {
	0% {
		transform: rotateY(180deg);
		opacity: 0;
	}
	20% {
		opacity: 1;
	}
	35%,
	100% {
		opacity: 0;
	}
	50%,
	100% {
		transform: rotateY(0deg);
	}
}
@keyframes page-3 {
	15% {
		transform: rotateY(180deg);
		opacity: 0;
	}
	35% {
		opacity: 1;
	}
	50%,
	100% {
		opacity: 0;
	}
	65%,
	100% {
		transform: rotateY(0deg);
	}
}
@keyframes page-4 {
	30% {
		transform: rotateY(180deg);
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	65%,
	100% {
		opacity: 0;
	}
	80%,
	100% {
		transform: rotateY(0deg);
	}
}
@keyframes page-5 {
	45% {
		transform: rotateY(180deg);
		opacity: 0;
	}
	65% {
		opacity: 1;
	}
	80%,
	100% {
		opacity: 0;
	}
	95%,
	100% {
		transform: rotateY(0deg);
	}
}
